<template>
  <el-row :gutter="10">
    <el-col :span="spanSize" :offset="offsetSize">
      <div class="title">
        <h3>
          <a
            href="https://go-sponge.com/zh-cn/assistant?id=%e7%94%9f%e6%88%90%e4%bb%a3%e7%a0%81"
            target="_blank"
            style="color: #00AEB1;"
            >{{ title }}</a
          >
        </h3>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="大语言类型" prop="llmType">
            <el-select
              style="width: 80%; font-size: medium"
              v-model="ruleForm.llmType"
              placeholder="请选择大语言类型"
              filterable
              @blur="selectBlur"
              @clear="selectClear"
              @change="selectChangeLLMType"
            >
              <el-option
                v-for="(item, index) in llmTypeOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                注：国外的大语言类型需要开启网络代理。
              </div>
            </el-tooltip>
          </el-form-item>

          <el-form-item label="模型" prop="llmModel">
            <el-select
              style="width: 80%; font-size: medium"
              v-model="ruleForm.llmModel"
              placeholder="请选择模型"
              filterable
              allow-create
              clearable
              @blur="selectBlur"
              @clear="selectClear"
              @change="selectChange"
            >
              <el-option
                v-for="(item, index) in llmModelOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                支持手动输入或选择已有模型。
              </div>
            </el-tooltip>
          </el-form-item>

          <el-form-item label="api key" prop="apiKey">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.apiKey"
              :type="showPassword ? 'text' : 'password'"
              placeholder="与大语言类型对应的api key"
            >
              <template #append>
                <el-button
                  :icon="showPassword ? 'el-icon-view' : 'el-icon-view'"
                  @click="togglePassword"
                />
              </template>
            </el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                填写大语言类型对应的API Key，用于调用相应的服务。
              </div>
            </el-tooltip>
          </el-form-item>

          <el-form-item
            label="工作目录"
            prop="goDir"
            style="margin-top: 10px;"
            v-if="!ruleForm.isSpecifiedGoFile"
          >
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.goDir"
              placeholder="请输入工作目录"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                Go项目代码所在文件夹的绝对路径。<br />
                windows环境示例：D:\project\dir<br />
                linux环境示例：/project/dir<br />
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item
            label="Go文件"
            prop="goFile"
            v-if="ruleForm.isSpecifiedGoFile"
          >
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.goFile"
              placeholder="请输入Go文件路径"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                指定Go文件的绝对路径，<br />
                windows环境示例：D:\project\dir\main.go<br />
                linux环境示例：/project/dir/main.go<br />
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="指定Go文件">
            <el-switch v-model="ruleForm.isSpecifiedGoFile"></el-switch>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                如果指定了Go文件，工作目录将被忽略。
              </div>
            </el-tooltip>
          </el-form-item>
        </el-form>
        <div style="margin: 30px auto;"></div>

        <div class="conform-btns">
          <el-button size="default" @click="clearFormData">重置</el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            :disabled="isShowingInfo"
            :loading="isShowingInfo"
            >生成代码</el-button
          >
          <el-button size="default" v-show="isShowResultBtn" @click="showResult"
            >查看结果</el-button
          >
        </div>
      </div>

      <el-dialog
        top="5vh"
        title="执行结果"
        :visible.sync="showResultDialogVisible"
        width="66%"
        center
      >
        <el-input
          type="textarea"
          :rows="20"
          autosize
          readonly
          v-model="responseContent"
        >
        </el-input>
      </el-dialog>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
export default {
  name: "AssistantGenerate",
  data() {
    return {
      title: "AI助手基于函数注释生成代码",
      path: "assistant-generate",
      labelPosition: "right",
      windowWidth: document.documentElement.clientWidth,
      spanSize: 16,
      offsetSize: 4,
      isDownloading: false,
      showPassword: false,

      isShowingInfo: false,
      responseContent: "",
      showResultDialogVisible: false,
      isShowResultBtn: false,

      ruleForm: {
        llmType: "",
        llmModel: "",
        apiKey: "",
        goDir: "",
        goFile: "",
        isSpecifiedGoFile: false,
      },
      rules: {
        llmType: [
          { required: true, message: "请选择大语言类型", trigger: "blur" },
        ],
        llmModel: [{ required: true, message: "请选择模型", trigger: "blur" }],
        apiKey: [{ required: true, message: "请输入API Key", trigger: "blur" }],
        goDir: [{ required: true, message: "请输入工作目录", trigger: "blur" }],
        goFile: [
          { required: true, message: "请输入Go文件路径", trigger: "blur" },
        ],
      },

      llmTypeOptions: [
        { label: "depseek", value: "deepseek" },
        { label: "hatgpt", value: "chatgpt" },
        { label: "gemini", value: "gemini" },
      ],
      llmModelOptions: [],
      allLLMOptions: {
        deepseek: [
          { label: "deepseek-chat", value: "deepseek-chat" },
          { label: "deepseek-reasoner", value: "deepseek-reasoner" },
        ],
        chatgpt: [
          { label: "gpt-4o", value: "gpt-4o" },
          { label: "gpt-4o-mini", value: "gpt-4o-mini" },
          { label: "gpt-4-turbo", value: "gpt-4-turbo" },
          { label: "gpt-4", value: "gpt-4" },
          { label: "o1-mini", value: "o1-mini" },
          { label: "o1-preview", value: "o1-preview" },
        ],
        gemini: [
          {
            label: "gemini-2.5-pro-exp-03-25",
            value: "gemini-2.5-pro-exp-03-25",
          },
          { label: "gemini-2.0-flash", value: "gemini-2.0-flash" },
          {
            label: "gemini-2.0-flash-thinking-exp",
            value: "gemini-2.0-flash-thinking-exp",
          },
          { label: "gemini-2.0-pro-exp", value: "gemini-2.0-pro-exp" },
        ],
      },
    };
  },
  created: function() {
    this.adaptiveWindows();
    this.listLLM();

    const apiUrl = appConfig.spongeServiceAddr + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.llmType = data.data.llmType;
          this.ruleForm.llmModel = data.data.llmModel;
          this.ruleForm.apiKey = data.data.apiKey;
          this.ruleForm.goDir = data.data.goDir;
          this.ruleForm.goFile = data.data.goFile;
          this.ruleForm.isSpecifiedGoFile = data.data.isSpecifiedGoFile;
          this.selectChangeLLMType(this.ruleForm.llmType);
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.updateIsShowingInfo(true);
          const apiUrl = appConfig.spongeServiceAddr + "/assistant";
          var arg = "";
          if (this.ruleForm.isSpecifiedGoFile) {
            arg = `assistant generate --type=${this.ruleForm.llmType} --model=${this.ruleForm.llmModel} --api-key=${this.ruleForm.apiKey} --file=${this.ruleForm.goFile}`;
          } else {
            arg = `assistant generate --type=${this.ruleForm.llmType} --model=${this.ruleForm.llmModel} --api-key=${this.ruleForm.apiKey} --dir=${this.ruleForm.goDir}`;
          }
          const bodyData = {
            path: this.path,
            arg: arg,
          };
          axios
            .post(apiUrl, bodyData)
            .then(res => {
              this.updateIsShowingInfo(false);
              if (res.data.code == 0) {
                this.responseContent = res.data.data;
                this.showResultDialogVisible = true;
                this.isShowResultBtn = true;
              } else {
                this.$message.error(res.data.msg);
              }
            })
            .catch(err => {
              this.updateIsShowingInfo(false);
              this.$message.error(err.response.data.msg);
            });
        } else {
          return false;
        }
      });
    },
    updateIsDownloading(value) {
      this.isDownloading = value;
    },
    clearFormData() {
      this.ruleForm = {};
    },

    listLLM() {
      const apiUrl = appConfig.spongeServiceAddr + "/listLLM";
      axios
        .get(apiUrl)
        .then(res => {
          const { data } = res;
          if (data.code == 0) {
            this.llmTypeOptions = data.data.llmTypeOptions;
            this.allLLMOptions = data.data.allLLMOptions;
          }
        })
        .catch(err => {
          // console.log(err);
        });
    },

    selectBlur(e) {
      if (e.target.value !== "") {
        this.value = e.target.value + "(其他)";
        this.$forceUpdate(); // 强制更新
      }
    },
    selectClear() {
      this.value = "";
      this.$forceUpdate();
    },
    selectChange(val) {
      this.value = val;
      this.$forceUpdate();
    },
    selectChangeLLMType(val) {
      this.value = val;
      this.llmModelOptions = this.allLLMOptions[val] || [];
      if (this.llmModelOptions.length > 0) {
        this.ruleForm.llmModel = this.llmModelOptions[0].value;
      }
      this.$forceUpdate();
    },

    togglePassword() {
      this.showPassword = !this.showPassword;
    },

    updateIsShowingInfo(value) {
      this.isShowingInfo = value;
    },

    showResult() {
      this.showResultDialogVisible = true;
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
      } else if (this.windowWidth <= 1200) {
        this.spanSize = 18;
        this.offsetSize = 3;
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 25px !important;
}

.form-style {
  border: 2px solid #ddd;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 10px;
  padding: 40px 40px;
}

.form-style /deep/ .el-form-item__label {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  color: #111;
}

.el-select /deep/ .el-input__inner {
  font-size: medium;
}

.el-icon-question {
  font-size: 20px;
  vertical-align: middle;
  color: #dcdfe6;
}

.tooltip-content {
  padding: 5px;
  color: #c46600;
  font-size: 14px;
  line-height: 150%;
}

.el-dialog__wrapper /deep/ .el-dialog .el-dialog__body {
  padding: 10px 50px 50px 50px;
}

.el-textarea /deep/ .el-textarea__inner {
  background: rgba(220, 220, 200, 0.3);
  border: 1px solid #ddd;
}

.title a {
  text-decoration: none;
}
.title a:hover {
  text-decoration: underline;
}
</style>



