<template>
  <el-row :gutter="10">
    <el-col :span="18" :offset="3">
      <p style="margin-bottom: 30px;line-height: 150%;">
        sponge 是一个强大的 Go 开发框架，其核心理念是通过解析 SQL、Protobuf 或
        JSON
        文件逆向生成模块化代码，这些模块代码可灵活组合成多种类型的完整后端服务（类似海绵细胞的特性，打散的海绵细胞能自动重新组合成新的海绵）。
        sponge
        拥有出色的项目工程化能力，提供一站式项目开发解决方案，涵盖代码生成、开发、测试、API
        文档生成和部署。sponge
        通过模块化架构与AI代码生成的深度融合，实现开发效率大幅提升效率，降低技术门槛，以"低代码"开发模式让开发者轻松构建稳定可靠的后端系统。
      </p>
      <p>
        github地址：<a
          href="https://github.com/go-dev-frame/sponge"
          target="_blank"
          >https://github.com/go-dev-frame/sponge</a
        >
      </p>
      <p>
        开发文档：<a href="https://go-sponge.com/zh-cn/" target="_blank"
          >https://go-sponge.com/zh-cn/</a
        >
      </p>
      <br />
      <p>
        <b>
          sponge 代码生成引擎。
        </b>
      </p>
      <p><b>1.</b> sponge 基于自带模板生成代码框架，如下图所示：</p>
      <div class="home-image">
        <el-image :src="homeImageSrc"></el-image>
      </div>
      <br />
      <p><b>2.</b> sponge 基于自定义模板生成代码框架，如下图所示：</p>
      <div class="framework-image">
        <el-image :src="homeTplImageSrc"></el-image>
      </div>
      <br />
      <p><b>3.</b> sponge 基于函数描述信息生成业务逻辑代码框架，如下图所示：</p>
      <div class="framework-image">
        <el-image :src="homeAssistantImageSrc"></el-image>
      </div>
      <br />
      <p></p>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      homeImageSrc: require("@/assets/sponge-framework.png"),
      homeTplImageSrc: require("@/assets/template-framework.png"),
      homeAssistantImageSrc: require("@/assets/ai-assistant-framework.png"),
    };
  },
};
</script>

<style>
.home-image {
  margin: auto;
}

.framework-image {
  width: 80%;
  margin: auto;
}

.key-word {
  /* background-color: #ddd; */
  color: #c7254e;
}
</style>



