<template>
  <el-row :gutter="10">
    <el-col :span="18" :offset="3">
      <p style="margin-bottom: 30px;line-height: 150%;">
        Sponge is a powerful Go development framework. Its core concept is to
        generate modular code by parsing SQL, Protobuf, or JSON files in
        reverse, these modular codes can be flexibly combined into various types
        of complete backend services. Sponge offers outstanding project
        engineering capabilities, providing an all-in-one project development
        solution that covers code generation, development, testing, API
        documentation generation, and deployment. Sponge significantly enhances
        development efficiency and reduces technical barriers through the deep
        integration of modular architecture and AI-generated code. Its
        "low-code" development model enables developers to effortlessly build
        stable and reliable backend systems.
      </p>
      <p>
        Github Repository:
        <a href="https://github.com/go-dev-frame/sponge" target="_blank"
          >https://github.com/go-dev-frame/sponge</a
        >
      </p>
      <p>
        Development Documentation:
        <a href="https://go-sponge.com" target="_blank"
          >https://go-sponge.com</a
        >
      </p>
      <br />
      <p>
        <b>
          Sponge code generation engine.
        </b>
      </p>
      <p>
        <b>1.</b> The code generation framework based on built-in templates, as
        shown in the following figure:
      </p>
      <div class="home-image">
        <el-image :src="homeImageSrc"></el-image>
      </div>
      <br />
      <p>
        <b>2.</b> Sponge generates code framework based on custom templates, as
        shown in the following figure:
      </p>
      <div class="framework-image">
        <el-image :src="homeTplImageSrc"></el-image>
      </div>
      <br />
      <p>
        <b>3.</b> Sponge generates business logic code framework based on
        function description information, as shown in the following figure:
      </p>
      <div class="framework-image">
        <el-image :src="homeAssistantImageSrc"></el-image>
      </div>
      <br />
      <p></p>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      homeImageSrc: require("@/assets/sponge-framework.png"),
      homeTplImageSrc: require("@/assets/template-framework.png"),
      homeAssistantImageSrc: require("@/assets/ai-assistant-framework.png"),
    };
  },
};
</script>

<style>
.home-image {
  margin: auto;
}

.framework-image {
  width: 80%;
  margin: auto;
}

.key-word {
  /* background-color: #ddd; */
  color: #c7254e;
}
</style>



